/* Client Credentials Container */
.client-credentials-container {
  padding: 20px;
}

.client-credentials-title {
  margin-bottom: 20px !important;
  color: #2f3b26;
  font-weight: bold !important;
}

/* Table Styles */
.client-credentials-table {
  width: 100%;
  border-collapse: collapse;
}

.table-header {
  background-color: #98a984 !important;
  color: #f4e6cd !important;
  font-weight: bold !important;
}

.client-credentials-table th {
  background-color: #f4f4f4;
  padding: 16px;
  text-align: left;
  color: #2f3b26;
}

.client-credentials-table td {
  padding: 12px;
  border-bottom: 1px solid #e0e0e0;
}

/* Action Cell Styles */
.client-credentials-table td[align="center"] {
  text-align: center !important;
}

.client-credentials-table td[align="center"] .MuiButton-root {
  margin: 0 4px;
}

/* Search Box Styles */
#search-wrapper {
  background-color: transparent;
  display: flex;
  align-items: center;
}

#image-wrapper {
  color: #2f3b26;
  padding: 5px;
  display: flex;
  align-items: center;
}

#search-input-client-credentials {
  background-color: rgb(244, 230, 205);
  border: none;
  font-size: 16px;
  margin-left: 10px;
  height: 40px;
  border-bottom: 2px solid #2f3b26;
  color: #2f3b26;
  width: 330px;
}

#search-input-client-credentials:focus {
  outline: none;
}

.custom-placeholder::placeholder {
  color: #2f3b26;
  opacity: 0.7;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  color: #666;
  background-color: #f4e6cd;
  border-radius: 8px;
  border: 2px solid #2f3b26;
}

/* Table MUI Overrides */
.client-credentials-container .MuiTableRow-root {
  background-color: #f4e6cd;
}

.client-credentials-container .MuiTableCell-root {
  color: #2f3b26 !important;
}

.client-credentials-container .MuiTableContainer-root {
  background-color: #f4e6cd;
  border-radius: 8px;
  border: 2px solid #2f3b26;
}

.client-credentials-container .MuiTablePagination-root {
  background-color: #2f3b26;
  color: #f4e6cd !important;
}

.client-credentials-container .MuiTablePagination-selectIcon,
.client-credentials-container .MuiTablePagination-actions button {
  color: #f4e6cd !important;
}

/* Common styles */
#outer-stack-client-credentials {
  width: 100%;
  margin-left: 10px;
  margin-top: 10px;
}

#outer-box-client-credentials {
  margin-bottom: 10px;
  height: 80vh;
  overflow-y: scroll;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: inset 0 -10px 10px -10px #000000;
  scrollbar-width: none;
  margin-right: 10px;
  width: 100%;
}

/* Hide scrollbar for Chrome, Safari and Opera */
#outer-box-client-credentials::-webkit-scrollbar {
  display: none;
}

/* Dialog overrides remain the same */

@media (max-width: 768px) {
  .credentials-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .credentials-header button {
    width: 100%;
  }

  .table-container {
    overflow-x: auto;
  }

  .MuiTableCell-root {
    padding: 8px;
    font-size: 14px;
  }

  .MuiButton-root {
    padding: 4px 8px;
    font-size: 12px;
  }

  .MuiTableCell-root .MuiButton-root {
    margin-right: 4px !important;
    margin-bottom: 4px;
  }

  .MuiTableCell-root[align="center"] {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 600px) {
  .MuiTable-root {
    display: block;
  }

  .MuiTableHead-root {
    display: none;
  }

  .MuiTableBody-root {
    display: block;
  }

  .MuiTableRow-root {
    display: block;
    border: 1px solid #98a984;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #fff;
    border-radius: 4px;
  }

  .MuiTableCell-root {
    display: block;
    text-align: left !important;
    padding: 5px 0 !important;
    border: none !important;
  }

  .MuiTableCell-root:before {
    content: attr(data-label);
    font-weight: bold;
    display: inline-block;
    width: 120px;
    color: #2f3b26;
  }

  .MuiTableCell-root[align="center"] {
    padding-top: 10px !important;
  }

  .MuiTableCell-root[align="center"]:before {
    display: none;
  }
}

.MuiDialog-paper {
  background-color: #f4e6cd !important;
}

.MuiDialogTitle-root {
  background-color: #98a984;
  color: #f4e6cd;
}

.MuiDialogContent-root {
  padding-top: 20px !important;
}

.MuiDialogContentText-root {
  color: #2f3b26;
}