.client-credentials-container {
  padding: 20px;
}

.client-credentials-title {
  margin-bottom: 20px !important;
  color: #2f3b26;
  font-weight: bold !important;
}

.client-credentials-list {
  width: 100%;
}

.client-credentials-table {
  width: 100%;
  border-collapse: collapse;
}

.table-header {
  background-color: #98a984 !important;
  color: #f4e6cd !important;
  font-weight: bold !important;
}

.client-credentials-table th {
  background-color: #f4f4f4;
  padding: 16px;
  text-align: left;
  color: #2f3b26;
}

.client-credentials-table td {
  padding: 12px;
  border-bottom: 1px solid #e0e0e0;
}

.credential-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  color: #666;
  background-color: #f4e6cd;
}

/* Search Box Styles */
#search-wrapper {
  background-color: transparent;
}

#image-wrapper {
  color: #2f3b26;
  padding: 5px;
  display: flex;
  align-items: center;
}

#search-input-credentials {
  background-color: rgb(244, 230, 205);
  border: none;
  font-size: 16px;
  margin-left: 10px;
  height: 40px;
  border-bottom: 2px solid #2f3b26;
  color: #2f3b26;
}

#search-input-credentials:focus {
  outline: none;
}

.custom-placeholder::placeholder {
  color: #2f3b26;
  opacity: 0.7;
}

/* Table Styles */
.MuiTableRow-root {
  background-color: #f4e6cd;
}

.MuiTableCell-root {
  color: #2f3b26 !important;
}

.MuiTableContainer-root {
  background-color: #f4e6cd;
  border-radius: 8px;
  border: 2px solid #2f3b26;
}

.MuiTablePagination-root {
  background-color: #2f3b26;
  color: #f4e6cd !important;
}

.MuiTablePagination-selectIcon,
.MuiTablePagination-actions button {
  color: #f4e6cd !important;
}

/* Common styles - identical to SecretKeys */
#outer-stack {
  width: 100%;
  margin-left: 10px;
  margin-top: 10px;
}

#outer-box {
  margin-bottom: 10px;
  height: 80vh;
  overflow-y: scroll;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: inset 0 -10px 10px -10px #000000;
  scrollbar-width: none;
  margin-right: 10px;
  width: 100%;
}

/* Hide scrollbar for Chrome, Safari and Opera */
#outer-box::-webkit-scrollbar {
  display: none;
}

/* Chip styles for status */
.status-active {
  background-color: #2f3b26 !important;
  color: #f4e6cd !important;
}

.status-inactive {
  background-color: #98a984 !important;
  color: #f4e6cd !important;
}

/* Button overrides for consistency */
.MuiButton-root {
  text-transform: none !important;
}

.MuiButton-containedPrimary {
  background-color: #2f3b26 !important;
  color: #f4e6cd !important;
}

.MuiButton-outlinedPrimary {
  border-color: #2f3b26 !important;
  color: #2f3b26 !important;
}